// Global

img {
  max-width: 100%;
  height: auto;
}

// Visually-hidden

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// Reset

* {
  padding: 0;
  margin: 0;
  border: 0;
}

input,
button {
  font-family: inherit;
}

button {
  cursor: pointer;
}

ul li {
  list-style: none;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

nav {
  display: block;
}
